header {
    text-align: center;
    height: 100%;
    margin: auto;
    color: white;
    -webkit-box-shadow: 0px 1px 10px 2px #29648A;
            box-shadow: 0px 1px 10px 2px #29648A;
    display: block;
    position: relative;
    background-color: #29648A;
    opacity: 0.70;
    z-index: 2;
}
@media screen and (max-width: 650px) {
    #header {
        font-size: 28px;
    }
}


